<template>
  <div class="goodsattribute">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>销售管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">销售政策</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>促销政策使用情况</el-breadcrumb-item>
    </el-breadcrumb>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" filterable @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <el-input v-model="params.customer_name" maxlength="100" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker @change="clearTime" v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>

          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.order_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" max-height="600">
        <el-table-column :show-overflow-tooltip="true" prop="customer_name" label="客户名称" width="180">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="order_sn" label="单据号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="discount_price" label="折让金额" align="right">
          <template slot-scope="scope">
            {{scope.row.discount_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="business_time" label="业务日期">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="add_time" label="单据时间">
        </el-table-column>
      </el-table>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="10" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: 'salesSalesbeforeLookuser',
  data() {
    return {
      params: {
        // 查询
        customer_id: "", //客户id
        start_time: "", //开始时间
        end_time: "", //结束时间
        company_id: "", //组织ID
        order_sn: "" //订单号
      }
    };
  },
  methods: {
    ...mapActions([
      "policyLookuser_hide",
      "promotionpolicyIndexACT",
      "policyAdd_hide"
    ]),
    clearTime(val) {
      if (val == null) {
        delete this.searchParams.start_time;
        delete this.searchParams.end_time;
      }
    },
    goBack() {
      this.$router.push({ path: "/sales/promotionpolicy" });
    },
    onReset() {
      this.$set(this, "params", {
        policy_id: window.localStorage.getItem("policy_id")
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: { policy_id: window.localStorage.getItem("policy_id") },
        TableData: this.TableData, //列表数据
        API: "getOrderPolicyList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: { policy_id: window.localStorage.getItem("policy_id") }, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOrderPolicyList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取促销政策列表
  }
};
</script>
